body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-color: #0f0f0f;
  --secondary-bg-color: #222222;
  --hover-color: #3f3f3f;
  --font-color: #ffffff;
}
html {
  font-size: 62.5%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  background-color: #0f0f0f;
  background-color: var(--bg-color);
  color: #ffffff;
  color: var(--font-color);
  font-family: "Inter", sans-serif;
}
a {
  text-decoration: none;
  color: #ffffff;
  color: var(--font-color);
}

.navbar {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-gap: 5rem;
  gap: 5rem;
  padding: 1rem 2rem;
  /* width: 100vw; */
  position: -webkit-sticky;
  position: sticky;
  height: 60px;
  top: 0;
  background-color: var(--bg-color);
  z-index: 100;
}
.navbar a {
  position: absolute;
  left: 3rem;
}
.navbar_search {
  display: flex;
  align-items: center;
  flex-direction: row;
  position: relative;
}
.navbar_search input {
  height: 4rem;
  width: 60rem;
  color: var(--font-color);
  background-color: var(--bg-color);
  border: 2px solid var(--secondary-bg-color);
  border-radius: 2rem;
  padding: 0 3rem;
  font-family: "Inter", sans-serif;
  font-weight: 300;
  font-size: 1.6rem;
}
.navbar_search button {
  height: 4rem;
  width: 6rem;
  padding-top: 0.6rem;
  border-radius: 2rem;
  background-color: var(--secondary-bg-color);
  color: var(--font-color);
  border: 2px solid var(--secondary-bg-color);
  position: absolute;
  right: 0;
}

@media only screen and (max-width: 940px) {
  .navbar {
    position: -webkit-sticky;
    position: sticky;
  }
  .navbar_search input {
    width: 30rem;
  }
}
@media only screen and (max-width: 400px) {
  .navbar_search input {
    width: 15rem;
  }
  .navbar_search button {
    width: 3.5rem;
  }
  .navbar img {
    left: 1rem;
  }
}
